import React, { Component } from 'react';
import { render } from 'react-dom';
import * as tf from '@tensorflow/tfjs';
import DrawableCanvas from './drawable-canvas';
import ImageRecognition from './image-recognition';
import './style.css';

const CANVAS_WIDTH = 400;
const CANVAS_HEIGHT = 400;

class App extends Component {
  render() {
    return (
      <div style={{ textAlign: 'center'}}>
        <h4>
           Using a trained MNIST Keras model, with Tensorflow.js and React.js
        </h4>

        <ImageRecognition
           width={CANVAS_WIDTH}
           height={CANVAS_HEIGHT}
        >
        {
          (predictedNumber, predictions) => (
            <h1>{predictedNumber}</h1>
          )
        }
        </ImageRecognition>
      </div>
    );
  }
}

render(<App />, document.getElementById('root'));



